import React, { useState, useEffect } from 'react';
import { Button, Input, Typography, Table, Tag, Spin, Card, Collapse, Toast, Space, Tabs } from '@douyinfe/semi-ui';
import { IconSearch, IconCopy, IconDownload, IconSetting } from '@douyinfe/semi-icons';
import { API, timestamp2string } from '../helpers';
import { stringToColor } from '../helpers/render';
import { ITEMS_PER_PAGE } from '../constants';
import { renderModelPrice, renderQuota } from '../helpers/render';
import Paragraph from '@douyinfe/semi-ui/lib/es/typography/paragraph';
import { Tooltip, Modal } from '@douyinfe/semi-ui';
import Papa from 'papaparse';

const { Text } = Typography;
const { Panel } = Collapse;
const { TabPane } = Tabs;

function renderTimestamp(timestamp) {
    return timestamp2string(timestamp);
}

function renderIsStream(bool) {
    if (bool) {
        return <Tag color="blue" size="large"></Tag>;
    } else {
        return <Tag color="purple" size="large"></Tag>;
    }
}

function renderUseTime(type) {
    const time = parseInt(type);
    if (time < 101) {
        return <Tag color="green" size="large"> {time}  </Tag>;
    } else if (time < 300) {
        return <Tag color="orange" size="large"> {time}  </Tag>;
    } else {
        return <Tag color="red" size="large"> {time}  </Tag>;
    }
}

const LogsTable = () => {
    // 
    const [hasError, setHasError] = useState(false);
    const [errorMessage, setErrorMessage] = useState('');
    const [isInitializing, setIsInitializing] = useState(true);
    const [apikey, setAPIKey] = useState('');
    const [activeTabKey, setActiveTabKey] = useState('');
    const [tabData, setTabData] = useState({});
    const [loading, setLoading] = useState(false);
    const [activeKeys, setActiveKeys] = useState([]);
    const [pageSize, setPageSize] = useState(ITEMS_PER_PAGE);
    const [baseUrl, setBaseUrl] = useState('');
    const [customBaseUrl, setCustomBaseUrl] = useState('');
    const [showCustomInput, setShowCustomInput] = useState(false);
    
    // 
    const getInitialBaseUrls = () => {
        // 
        const defaultUrls = { 'NewAPI': 'https://your-newapi-domain.com' };
        
        try {
            const envUrl = process.env.REACT_APP_BASE_URL;
            if (!envUrl || !envUrl.trim()) {
                return defaultUrls;
            }
            
            const trimmedUrl = envUrl.trim();
            
            // JSON
            if (trimmedUrl.startsWith('{') && trimmedUrl.endsWith('}')) {
                try {
                    const parsed = JSON.parse(trimmedUrl);
                    // 
                    if (parsed && typeof parsed === 'object' && Object.keys(parsed).length > 0) {
                        return parsed;
                    }
                } catch (jsonError) {
                    // JSONURL
                }
            }
            
            // URL
            if (trimmedUrl.startsWith('http')) {
                return { 'NewAPI': trimmedUrl };
            }
            
            return defaultUrls;
        } catch (error) {
            // 
            return defaultUrls;
        }
    };
    
    const [baseUrls, setBaseUrls] = useState(() => getInitialBaseUrls());

    useEffect(() => {
        try {
            // baseUrl
            const keys = Object.keys(baseUrls);
            if (keys.length > 0) {
                const firstKey = keys[0];
                setActiveTabKey(firstKey);
                setBaseUrl(baseUrls[firstKey]);
            }
            setIsInitializing(false);
        } catch (error) {
            console.error('Error in useEffect:', error);
            setHasError(true);
            setErrorMessage('' + error.message);
            setIsInitializing(false);
        }
    }, [baseUrls]);

    const handleTabChange = (key) => {
        setActiveTabKey(key);
        setBaseUrl(baseUrls[key]);
    };

    // const testConnection = async (testUrl) => {
    //     console.log(':', testUrl);
    //     try {
    //         // 
    //         const response = await fetch(testUrl, {
    //             method: 'GET',
    //             mode: 'cors',
    //         });
    //         console.log(':', response.status);
    //         return response.ok;
    //     } catch (error) {
    //         console.error(':', error);
    //         return false;
    //     }
    // };

    const addCustomBaseUrl = async () => {
        if (!customBaseUrl.trim()) {
            Toast.warning('BASE_URL');
            return;
        }
        
        // URL
        let testUrl = customBaseUrl.trim();
        try {
            new URL(testUrl);
        } catch (e) {
            Toast.error('URL');
            return;
        }
        
        // 
        if (testUrl.endsWith('/')) {
            testUrl = testUrl.slice(0, -1);
        }
        
        const newKey = `Custom_${Date.now()}`;
        const newBaseUrls = {
            ...baseUrls,
            [newKey]: testUrl
        };
        
        setBaseUrls(newBaseUrls);
        setActiveTabKey(newKey);
        setBaseUrl(testUrl);
        setCustomBaseUrl('');
        setShowCustomInput(false);
        Toast.success('BASE_URL');
    };

    const removeCustomUrl = (key) => {
        const newBaseUrls = { ...baseUrls };
        delete newBaseUrls[key];
        setBaseUrls(newBaseUrls);
        
        // tab
        if (key === activeTabKey) {
            const remainingKeys = Object.keys(newBaseUrls);
            if (remainingKeys.length > 0) {
                const firstKey = remainingKeys[0];
                setActiveTabKey(firstKey);
                setBaseUrl(newBaseUrls[firstKey]);
            }
        }
        Toast.success('URL');
    };

    // const resetData = (key) => {
    //     setTabData((prevData) => ({
    //         ...prevData,
    //         [key]: {
    //             balance: 0,
    //             usage: 0,
    //             accessdate: "",
    //             logs: [],
    //             tokenValid: false,
    //         }
    //     }));
    // };

    const fetchData = async () => {
        console.log('fetchData ');
        console.log('API:', apikey);
        console.log('BaseURL:', baseUrl);
        
        if (apikey === '') {
            Toast.warning('');
            return;
        }
        
        if (!baseUrl || baseUrl.trim() === '') {
            Toast.error('API');
            return;
        }
        
        // 
        const tokenLength = apikey.length;
        const tokenPrefix = apikey.substring(0, 10);
        console.log(':', tokenLength, ':', tokenPrefix);
        
        // 
        const isOpenAIFormat = /^sk-[a-zA-Z0-9]{48,}$/.test(apikey);
        const isSessionFormat = /^sess-[a-zA-Z0-9_-]{20,}$/.test(apikey);
        const isCustomFormat = apikey.length >= 20;
        
        if (isOpenAIFormat) {
            console.log('OpenAI');
        } else if (isSessionFormat) {
            console.log('Session');
        } else if (isCustomFormat) {
            console.log('');
        } else if (tokenLength < 10) {
            Toast.warning('');
        } else {
            console.log('');
        }
        
        console.log('loading');
        setLoading(true);
        let newTabData = { ...tabData[activeTabKey], balance: 0, usage: 0, accessdate: 0, logs: [], tokenValid: false };

        try {
            console.log('');
            if (process.env.REACT_APP_SHOW_BALANCE === "true") {
                console.log('SHOW_BALANCE=true');
                const subscriptionUrl = `${baseUrl}/v1/dashboard/billing/subscription`;
                console.log('URL:', subscriptionUrl);
                
                const subscription = await API.get(subscriptionUrl, {
                    headers: { Authorization: `Bearer ${apikey}` },
                });
                console.log(':', subscription.data);
                
                const subscriptionData = subscription.data;
                newTabData.balance = subscriptionData.hard_limit_usd;
                newTabData.tokenValid = true;

                console.log('');
                let now = new Date();
                let start = new Date(now.getTime() - 100 * 24 * 3600 * 1000);
                let start_date = `${start.getFullYear()}-${start.getMonth() + 1}-${start.getDate()}`;
                let end_date = `${now.getFullYear()}-${now.getMonth() + 1}-${now.getDate()}`;
                
                const usageUrl = `${baseUrl}/v1/dashboard/billing/usage?start_date=${start_date}&end_date=${end_date}`;
                console.log('URL:', usageUrl);
                
                const res = await API.get(usageUrl, {
                    headers: { Authorization: `Bearer ${apikey}` },
                });
                console.log(':', res.data);
                
                const data = res.data;
                newTabData.usage = data.total_usage / 100;
            } else {
                console.log('SHOW_BALANCE=false');
            }
        } catch (e) {
            console.error('Balance fetch error:', e);
            console.error(':', e.response?.data);
            console.error(':', e.response?.status);
            Toast.error(`: ${e.response?.data?.error?.message || e.message}`);
            // 
        }
        try {
            console.log('');
            if (process.env.REACT_APP_SHOW_DETAIL === "true") {
                console.log('SHOW_DETAIL=true');
                const logUrl = `${baseUrl}/api/log/token?key=${apikey}`;
                console.log('URL:', logUrl);
                
                const logRes = await API.get(logUrl);
                console.log(':', logRes.data);
                
                const { success, data: logData } = logRes.data;
                if (success) {
                    console.log(':', logData?.length);
                    newTabData.logs = logData.reverse();
                    newTabData.tokenValid = true;  // token
                    setActiveKeys(['1', '2']); // 
                } else {
                    console.log(':', logRes.data);
                    Toast.error('API');
                }
            } else {
                console.log('SHOW_DETAIL=false');
            }
        } catch (e) {
            console.error('Log fetch error:', e);
            console.error(':', e.response?.data);
            console.error(':', e.response?.status);
            Toast.error(`: ${e.response?.data?.message || e.message}`);
        }
        
        console.log('');
        setTabData((prevData) => ({
            ...prevData,
            [activeTabKey]: newTabData,
        }));
        setLoading(false);
        console.log('');

    };

    const copyText = async (text) => {
        try {
            // Try modern clipboard API first
            if (navigator.clipboard && window.isSecureContext) {
                await navigator.clipboard.writeText(text);
                Toast.success('' + text);
                return;
            }
            
            // Fallback for Safari and older browsers
            const textArea = document.createElement('textarea');
            textArea.value = text;
            textArea.style.position = 'fixed';
            textArea.style.left = '-999999px';
            textArea.style.top = '-999999px';
            document.body.appendChild(textArea);
            textArea.focus();
            textArea.select();
            
            try {
                document.execCommand('copy');
                textArea.remove();
                Toast.success('' + text);
            } catch (err) {
                textArea.remove();
                Modal.error({ title: '', content: text });
            }
        } catch (err) {
            Modal.error({ title: '', content: text });
        }
    };

    const columns = [
        {
            title: '',
            dataIndex: 'created_at',
            render: renderTimestamp,
            sorter: (a, b) => a.created_at - b.created_at,
        },
        {
            title: '',
            dataIndex: 'token_name',
            render: (text, record, index) => {
                return record.type === 0 || record.type === 2 ? (
                    <div>
                        <Tag
                            color="grey"
                            size="large"
                            onClick={() => {
                                copyText(text);
                            }}
                        >
                            {' '}
                            {text}{' '}
                        </Tag>
                    </div>
                ) : (
                    <></>
                );
            },
            sorter: (a, b) => ('' + a.token_name).localeCompare(b.token_name),
        },
        {
            title: '',
            dataIndex: 'model_name',
            render: (text, record, index) => {
                return record.type === 0 || record.type === 2 ? (
                    <div>
                        <Tag
                            color={stringToColor(text)}
                            size="large"
                            onClick={() => {
                                copyText(text);
                            }}
                        >
                            {' '}
                            {text}{' '}
                        </Tag>
                    </div>
                ) : (
                    <></>
                );
            },
            sorter: (a, b) => ('' + a.model_name).localeCompare(b.model_name),
        },
        {
            title: '',
            dataIndex: 'use_time',
            render: (text, record, index) => {
                return record.model_name.startsWith('mj_') ? null : (
                    <div>
                        <Space>
                            {renderUseTime(text)}
                            {renderIsStream(record.is_stream)}
                        </Space>
                    </div>
                );
            },
            sorter: (a, b) => a.use_time - b.use_time,
        },
        {
            title: '',
            dataIndex: 'prompt_tokens',
            render: (text, record, index) => {
                return record.model_name.startsWith('mj_') ? null : (
                    record.type === 0 || record.type === 2 ? <div>{<span> {text} </span>}</div> : <></>
                );
            },
            sorter: (a, b) => a.prompt_tokens - b.prompt_tokens,
        },
        {
            title: '',
            dataIndex: 'completion_tokens',
            render: (text, record, index) => {
                return parseInt(text) > 0 && (record.type === 0 || record.type === 2) ? (
                    <div>{<span> {text} </span>}</div>
                ) : (
                    <></>
                );
            },
            sorter: (a, b) => a.completion_tokens - b.completion_tokens,
        },
        {
            title: '',
            dataIndex: 'quota',
            render: (text, record, index) => {
                return record.type === 0 || record.type === 2 ? <div>{renderQuota(text, 6)}</div> : <></>;
            },
            sorter: (a, b) => a.quota - b.quota,
        },
        {
            title: '',
            dataIndex: 'content',
            render: (text, record, index) => {
                let other = null;
                try {
                    if (record.other === '') {
                        record.other = '{}';
                    }
                    other = JSON.parse(record.other);
                } catch (e) {
                    return (
                        <Tooltip content="">
                            <Paragraph
                                ellipsis={{
                                    rows: 2,
                                }}
                            >
                                {text}
                            </Paragraph>
                        </Tooltip>
                    );
                }
                if (other == null) {
                    return (
                        <Paragraph
                            ellipsis={{
                                rows: 2,
                                showTooltip: {
                                    type: 'popover',
                                },
                            }}
                        >
                            {text}
                        </Paragraph>
                    );
                }
                let content = renderModelPrice(
                    record.prompt_tokens,
                    record.completion_tokens,
                    other.model_ratio,
                    other.model_price,
                    other.completion_ratio,
                    other.group_ratio,
                );
                return (
                    <Tooltip content={content}>
                        <Paragraph
                            ellipsis={{
                                rows: 2,
                            }}
                        >
                            {text}
                        </Paragraph>
                    </Tooltip>
                );
            },
        }
    ];

    const copyTokenInfo = (e) => {
        e.stopPropagation();
        const activeTabData = tabData[activeTabKey] || {};
        const { balance, usage, accessdate } = activeTabData;
        const info = `: ${balance === 100000000 ? '' : `${balance.toFixed(3)}`}
: ${balance === 100000000 ? '' : `${(balance - usage).toFixed(3)}`}
: ${balance === 100000000 ? '' : `${usage.toFixed(3)}`}
: ${accessdate === 0 ? '' : renderTimestamp(accessdate)}`;
        copyText(info);
    };

    const exportCSV = (e) => {
        e.stopPropagation();
        const activeTabData = tabData[activeTabKey] || { logs: [] };
        const { logs } = activeTabData;
        const csvData = logs.map(log => ({
            '': renderTimestamp(log.created_at),
            '': log.model_name,
            '': log.use_time,
            '': log.prompt_tokens,
            '': log.completion_tokens,
            '': log.quota,
            '': log.content,
        }));
        const csvString = '\ufeff' + Papa.unparse(csvData);
        
        try {
            const blob = new Blob([csvString], { type: 'text/csv;charset=utf-8' });
            const url = URL.createObjectURL(blob);
            const link = document.createElement('a');
            link.href = url;
            link.download = 'data.csv';
            
            // For Safari compatibility
            if (navigator.userAgent.indexOf('Safari') > -1 && navigator.userAgent.indexOf('Chrome') === -1) {
                link.target = '_blank';
                link.setAttribute('target', '_blank');
            }
            
            document.body.appendChild(link);
            link.click();
            document.body.removeChild(link);
            setTimeout(() => URL.revokeObjectURL(url), 100);
        } catch (err) {
            Toast.error('');
            console.error('Export failed:', err);
        }
    };

    const activeTabData = tabData[activeTabKey] || { logs: [], balance: 0, usage: 0, accessdate: "", tokenValid: false };

    const renderContent = () => (
        <>
            <Card style={{ marginTop: 24 }}>
                <div style={{ marginBottom: 16 }}>
                    <div style={{ display: 'flex', alignItems: 'center', gap: 12, marginBottom: 8 }}>
                        <Text strong>API</Text>
                        <Tag color="blue">{baseUrl}</Tag>
                        <Button 
                            icon={<IconSetting />} 
                            theme="borderless" 
                            onClick={() => setShowCustomInput(!showCustomInput)}
                        >
                            
                        </Button>
                    </div>
                    
                    {showCustomInput && (
                        <div style={{ display: 'flex', gap: 8, marginTop: 8 }}>
                            <Input
                                placeholder="BASE_URLhttps://api.example.com"
                                value={customBaseUrl}
                                onChange={setCustomBaseUrl}
                                style={{ flex: 1 }}
                            />
                            <Button type="primary" onClick={addCustomBaseUrl}>
                                
                            </Button>
                            <Button onClick={() => {
                                setShowCustomInput(false);
                                setCustomBaseUrl('');
                            }}>
                                
                            </Button>
                        </div>
                    )}
                    
                    <div style={{ marginTop: 8, fontSize: '12px', color: '#666' }}>
                        <Text type="secondary">
                             :  (sk-xxx, sess-xxx, NewAPI)
                        </Text>
                    </div>
                </div>
                
                <Input
                    showClear
                    value={apikey}
                    onChange={(value) => setAPIKey(value)}
                    placeholder="API (sk-xxxsess-xxx)"
                    prefix={<IconSearch />}
                    suffix={
                        <Button
                            type='primary'
                            theme="solid"
                            onClick={fetchData}
                            loading={loading}
                            disabled={apikey === ''}
                        >
                            
                        </Button>
                    }
                    onKeyDown={(e) => {
                        if (e.key === 'Enter') {
                            fetchData();
                        }
                    }}
                />
            </Card>
            <Card style={{ marginTop: 24 }}>
                <Collapse activeKey={activeKeys} onChange={(keys) => setActiveKeys(keys)}>
                    {process.env.REACT_APP_SHOW_BALANCE === "true" && (
                        <Panel
                            header=""
                            itemKey="1"
                            extra={
                                <Button icon={<IconCopy />} theme='borderless' type='primary' onClick={(e) => copyTokenInfo(e)} disabled={!activeTabData.tokenValid}>
                                    
                                </Button>
                            }
                        >
                            <Spin spinning={loading}>
                                <div style={{ marginBottom: 16 }}>
                                    <Text type="secondary">
                                        {activeTabData.balance === 100000000 ? "" : activeTabData.balance === "" || activeTabData.balance === undefined ? "" : `${activeTabData.balance.toFixed(3)}`}
                                    </Text>
                                    <br /><br />
                                    <Text type="secondary">
                                        {activeTabData.balance === 100000000 ? "" : activeTabData.balance === "" || activeTabData.usage === "" || activeTabData.balance === undefined || activeTabData.usage === undefined ? "" : `${(activeTabData.balance - activeTabData.usage).toFixed(3)}`}
                                    </Text>
                                    <br /><br />
                                    <Text type="secondary">
                                        {activeTabData.balance === 100000000 ? "" : activeTabData.usage === "" || activeTabData.usage === undefined ? "" : `${activeTabData.usage.toFixed(3)}`}
                                    </Text>
                                    <br /><br />
                                    <Text type="secondary">
                                        {activeTabData.accessdate === 0 ? '' : activeTabData.accessdate === "" ? '' : renderTimestamp(activeTabData.accessdate)}
                                    </Text>
                                </div>
                            </Spin>
                        </Panel>
                    )}
                    {process.env.REACT_APP_SHOW_DETAIL === "true" && (
                        <Panel
                            header=""
                            itemKey="2"
                            extra={
                                <div style={{ display: 'flex', alignItems: 'center' }}>
                                    <Tag shape='circle' color='green' style={{ marginRight: 5 }}>$1 = 50 0000 tokens</Tag>
                                    <Button icon={<IconDownload />} theme='borderless' type='primary' onClick={(e) => exportCSV(e)} disabled={!activeTabData.tokenValid || activeTabData.logs.length === 0}>
                                        CSV
                                    </Button>
                                </div>
                            }
                        >
                            <Spin spinning={loading}>
                                <Table
                                    columns={columns}
                                    dataSource={activeTabData.logs}
                                    pagination={{
                                        pageSize: pageSize,
                                        hideOnSinglePage: true,
                                        showSizeChanger: true,
                                        pageSizeOpts: [10, 20, 50, 100],
                                        onPageSizeChange: (pageSize) => setPageSize(pageSize),
                                        showTotal: (total) => ` ${total} `,
                                        showQuickJumper: true,
                                        total: activeTabData.logs.length,
                                        style: { marginTop: 12 },
                                    }}
                                />
                            </Spin>
                        </Panel>
                    )}
                </Collapse>
            </Card>
        </>
    );

    // 
    if (isInitializing) {
        return (
            <Card style={{ marginTop: 24 }}>
                <div style={{ textAlign: 'center', padding: '40px 20px' }}>
                    <Spin size="large" />
                    <div style={{ marginTop: 16 }}>
                        <Text type="secondary">...</Text>
                    </div>
                </div>
            </Card>
        );
    }

    // 
    if (hasError) {
        return (
            <Card style={{ marginTop: 24 }}>
                <div style={{ textAlign: 'center', padding: '40px 20px' }}>
                    <Text type="danger" style={{ fontSize: '18px', marginBottom: '16px', display: 'block' }}>
                        
                    </Text>
                    <Text type="secondary" style={{ marginBottom: '16px', display: 'block' }}>
                        {errorMessage}
                    </Text>
                    <Button 
                        type="primary"
                        onClick={() => {
                            setHasError(false);
                            setErrorMessage('');
                            setIsInitializing(true);
                            window.location.reload();
                        }}
                    >
                        
                    </Button>
                </div>
            </Card>
        );
    }

    try {
        return (
            <>
                {Object.keys(baseUrls).length > 1 ? (
                    <Tabs type="line" onChange={handleTabChange} activeKey={activeTabKey}>
                        {Object.entries(baseUrls).map(([key, url]) => {
                            const isCustom = key.startsWith('Custom_');
                            const tabTitle = (
                                <div style={{ display: 'flex', alignItems: 'center', gap: 4 }}>
                                    <span>{isCustom ? '' : key}</span>
                                    {isCustom && (
                                        <Button 
                                            type="danger" 
                                            theme="borderless" 
                                            size="small"
                                            onClick={(e) => {
                                                e.stopPropagation();
                                                removeCustomUrl(key);
                                            }}
                                            style={{ fontSize: '12px', padding: '2px 4px' }}
                                        >
                                            
                                        </Button>
                                    )}
                                </div>
                            );
                            
                            return (
                                <TabPane tab={tabTitle} itemKey={key} key={key}>
                                    {renderContent()}
                                </TabPane>
                            );
                        })}
                    </Tabs>
                ) : (
                    renderContent()
                )}
            </>
        );
    } catch (error) {
        console.error('Render error:', error);
        return (
            <Card style={{ marginTop: 24 }}>
                <div style={{ textAlign: 'center', padding: '40px 20px' }}>
                    <Text type="danger">{error.message}</Text>
                </div>
            </Card>
        );
    }
};

export default LogsTable;
